@import url(https://rsms.me/inter/inter-ui.css);

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: "Inter UI", sans-serif;
  text-align: center;
  color: rgb(0, 0, 0);
}

.titan-background {
  position: absolute;
  width: 100%;
  background-image: linear-gradient(-180deg, #b7faff 39%, #85f054e3 100%);
  overflow: hidden;
  z-index: 1;
  /* Make the rubber band scroll not show empty white space */
  top: -10%;
  height: 120%;
  min-height: 120vh;
  margin-bottom: -20%;
}

.stars {
  position: absolute;
  top: 0;
  width: 300vw;
  height: 100vh;
  -webkit-transform: translate(0%, 0%);
          transform: translate(0%, 0%);
  background-size: contain;
  background-repeat: repeat;
  -webkit-transform-origin: top left;
          transform-origin: top left;
}

.stars-L {
  background-image: url(/static/media/stars-L.717532da.svg);
  background-size: 890px;
  height: 750px;
  -webkit-animation: panning-stars 200s linear infinite alternate;
          animation: panning-stars 200s linear infinite alternate;
}

.stars-M {
  background-image: url(/static/media/stars-M.56763e09.svg);
  height: 530px;
  -webkit-animation: panning-stars 300s linear infinite alternate;
          animation: panning-stars 300s linear infinite alternate;
}

.stars-S {
  background-image: url(/static/media/stars-S.055efd5c.svg);
  height: 370px;
  -webkit-animation: panning-stars 400s linear infinite alternate;
          animation: panning-stars 400s linear infinite alternate;
}

@-webkit-keyframes panning-stars {
  from {
    -webkit-transform: translate(-20%, 0%);
            transform: translate(-20%, 0%);
  }
  to {
    -webkit-transform: translate(-80%, -40%);
            transform: translate(-80%, -40%);
  }
}

@keyframes panning-stars {
  from {
    -webkit-transform: translate(-20%, 0%);
            transform: translate(-20%, 0%);
  }
  to {
    -webkit-transform: translate(-80%, -40%);
            transform: translate(-80%, -40%);
  }
}

.marvellous-container {
  position: relative;
  z-index: 2;
  margin-top: 64px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 5 0px;
  width: 95%;
  max-width: 1200px;
  background-color: #f5f7e7;
  border-radius: 8px;
}

h1 {
  display: block;
  font-size: 80px;
  margin: 0 0 24px 0;
}

@media (max-width: 500px) {
  h1 {
    font-size: 16vw;
  }
}

h2 {
  font-weight: 500;
  font-size: 24px;
}

h3 {
  font-weight: 300;
  font-size: 24px;
}

a {
  color: inherit;
  text-decoration: underline;
}

.title-marvel {
  font-weight: bolder;
  color: #020103;
  letter-spacing: -6.7px;
  text-transform: uppercase;
  padding: 0 16px;
  background: #9b00f5;
}

.title-studios {
  position: relative;
  display: inline-block;
  font-weight: bold;
  color: #5a0000;
  letter-spacing: -4.5px;
  text-transform: uppercase;
  padding: 0 16px;
  border-color: #9b00f5;
  border-top: 8px solid;
  border-bottom: 8px solid;
  line-height: 1;
}

code {
  display: block;
  box-sizing: border-box;
  text-align: left;
  font-size: 18px;
  width: 60%;
  padding: 16px 24px;
  background: rgba(44, 37, 44, 0.8);
  border: 1px solid #7f797f;
  border-radius: 8px;
  margin-bottom: 16px;
  margin-top: 16px;
  margin-left: auto;
  margin-right: auto;
}

.header {
  margin-bottom: 104px;
}

.installation-instructions,
.usage-instructions {
  margin-top: 64px;
  padding-left: auto;
  padding-right: auto;
}

.listgroup {
  text-align: left;
  padding-left: 50px;
  padding-right: 50px;
}

.footer {
  margin-top: 224px;
}

